import type { ReactNode } from "react";
import css from "./LayoutNotes.module.css"

interface LayoutNotesProps {
  children: ReactNode; // основной контент (список заметок)
  sidebar: ReactNode;  // слот @sidebar
  modal?: ReactNode;   // слот @modal, может быть пустым
}

export default function LayoutNotes({ children, sidebar, modal }: LayoutNotesProps) {
  return (
    <div className="container">
      {sidebar && (
        <aside className="sidebar">
          {sidebar}
        </aside>
      )}
      <main className="notesWrapper">
        {children}
        {modal && (
          <div>
            {modal}
          </div>
        )}
      </main>
    </div>
  );
}
